  font-weight: 700;
  color: #000;
  text-transform: uppercase;
}
.formBox__text {
  display: block;
  padding-bottom: 10px;
  padding-left: 0;
  font-size: 17px;
  color: #000;
}
.formBox .form__line {
  margin: auto;
  margin-top: 0;
  margin-bottom: 14px;
  padding: 10px 15px;
  width: 290px;
  border: none;
  background: #ecf1f6;
  color: #272b35;
}
.formBox .btn {
  padding: 0 80px;
  margin: 15px 0;
  color: #fff;
}
