}
@keyframes arrow-bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-7px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    /* 		transform: translateY(-40px); */
  }
  100% {
    opacity: 0.8;
    /* 		transform: translateY(0px); */
  }
}
@keyframes light {
  0% {
    background: #55c202;
    box-shadow: 0px 0px 4px 2px #55c202;
  }
  20% {
    background: #fff;
    box-shadow: 0px 0px 4px 2px #fff;
  }
  40% {
    background: #ff405a;
    box-shadow: 0px 0px 4px 2px #ff405a;
  }
  60% {
    background: #fff;
    box-shadow: 0px 0px 4px 2px #fff;
  }
  80% {
    background: #00ffff;
    box-shadow: 0px 0px 4px 2px #00ffff;
  }
  100% {
    background: #fff;
    box-shadow: 0px 0px 4px 2px #fff;
  }
}
